import React from "react";
import * as Bootstrap from "reactstrap";
import * as Icon from "react-bootstrap-icons";
import Tag from "../components/Tag";

export default class ProjectView extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            toggled: {}
        }
        this.toggleDetail.bind(this);
        this.isDetailToggled.bind(this);
    }

    toggleDetail(key, state) {
        return (function () {
            let toggled = this.state.toggled;
            toggled[key] = state;
            this.setState({ toggled: toggled });
        }).bind(this);
    }

    isDetailToggled(key) {
        return this.state.toggled[key] ?? false;
    }

    render() {
        return (
            <Bootstrap.Row className="g-3">
                {this.props.project?.map((project, i) =>
                    <Bootstrap.Col key={i} size={12} md={6} lg={4}>
                        <Bootstrap.Card>
                            <Bootstrap.CardImg
                                top
                                src={project.image}
                                className="bg-secondary"
                                style={{ objectFit: project.imageFit ?? "none", height: 200 }}
                            />
                            <Bootstrap.CardBody>
                                <Bootstrap.CardTitle>
                                    <h4>{project.name}</h4>
                                    <h6 style={{ color: "gray" }}>{project.year}</h6>
                                    <div className="d-flex flex-warp">
                                        {project.tags.map((tag, i) => <Tag key={i}>{tag}</Tag>)}
                                    </div>
                                </Bootstrap.CardTitle>
                                <Bootstrap.CardText style={{ whiteSpace: "pre" }}>
                                    {project.description}
                                </Bootstrap.CardText>
                                <Bootstrap.Row className="g-1">
                                    {project.details &&
                                        <Bootstrap.Button
                                            color="primary"
                                            className="rounded"
                                            onClick={this.toggleDetail(i, true)}
                                        >
                                            More
                                        </Bootstrap.Button>
                                    }
                                    {project.github &&
                                        <Bootstrap.Button href={project.github} color="secondary" className=" rounded">
                                            <Icon.Github /> Github
                                        </Bootstrap.Button>
                                    }
                                </Bootstrap.Row>
                            </Bootstrap.CardBody>
                        </Bootstrap.Card>
                        {project.details &&
                            <Bootstrap.Modal isOpen={this.isDetailToggled(i)} fade centered>
                                <Bootstrap.ModalHeader close={<button class="btn-close" onClick={this.toggleDetail(i, false)} />}>
                                    {project.name}
                                </Bootstrap.ModalHeader>
                                <Bootstrap.ModalBody>
                                    {project.details}
                                </Bootstrap.ModalBody>
                            </Bootstrap.Modal>
                        }
                    </Bootstrap.Col>
                )}
            </Bootstrap.Row>
        );
    }
}