import React from "react";
import { 
    EnvelopeFill,
    PhoneFill
} from "react-bootstrap-icons"

export default class Banner extends React.Component {
    renderEmail() {
        if (this.props.email) {
            return (
                <p className="h6">
                    <EnvelopeFill/> 信箱： 
                    <a className="link-light" href={`mailto://${this.props.email}`}>{this.props.email}</a>
                </p>
            );
        }
    }

    renderPhone() {
        if (this.props.phone) {
            return (
                <p className="h6">
                    <PhoneFill/> 電話：
                    <span className="link-light">{this.props.phone}</span>
                </p>
            );
        }
    }
    
    render() {
        return (
            <div className="py-5 text-center bg-secondary text-white">
                <div className="d-inline-block text-start">
                    <p className="h2">{this.props.name}</p>
                    <p className="h5">{this.props.school} - {this.props.major}</p>
                    { this.renderEmail() }
                    { this.renderPhone() }
                </div>
            </div>
        );
    }
}