import React from "react";
import * as Bootstrap from "reactstrap";
import * as Icon from "react-bootstrap-icons";
import Banner from "./components/Banner";
import MainPage from "./pages/MainPage";
import { Profile } from "./types/Profile";
export default class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            collapsed: true,
            links: [
            // {
            //     name: "個人專案",
            //     href: "#projects-personal"
            // }, 
            {
                name: "學校作品",
                href: "#projects-school"
            }, 
            {
                name: "Github",
                href: Profile.github,
                icon: <Icon.Github/>
            }, {
                name: "個人筆記",
                href: Profile.personalNoteWeb,
                icon: <Icon.PencilFill/>
            }]
        };
        this.toggleNav = this.toggleNav.bind(this);
    }

    toggleNav() {
        this.setState({
            collapsed: !this.state.collapsed
        });
    }

    render() {
        return (
            <div>
                <Bootstrap.Navbar dark color='dark' expand='md' fixed="top">
                    <Bootstrap.NavbarBrand tag='span'>{Profile.name}</Bootstrap.NavbarBrand>
                    <Bootstrap.NavbarToggler onClick={this.toggleNav} />

                    <Bootstrap.Collapse navbar isOpen={this.state.collapsed}>
                        <Bootstrap.Nav navbar>
                            {this.state.links.map((link, i) =>
                                <Bootstrap.NavItem key={i}>
                                    <Bootstrap.NavLink href={link.href}>
                                        {link.icon} {link.name}
                                    </Bootstrap.NavLink>
                                </Bootstrap.NavItem>
                            )}
                        </Bootstrap.Nav>
                    </Bootstrap.Collapse>
                </Bootstrap.Navbar>
                <div className="py-4"></div>
                <Banner
                    name={Profile.name}
                    school={Profile.school}
                    major={Profile.major}
                    email={Profile.email}
                    phone={Profile.phone}
                />
                <div className="pt-5" style={{ backgroundColor: ":rgb(230, 230, 230)" }}>
                    <Bootstrap.Container>
                        <MainPage />
                    </Bootstrap.Container>
                </div>
            </div>
        );
    }
}