import React from "react";
import * as Bootstrap from "reactstrap";
import Section from "../components/Section";
import Tag from "../components/Tag";
import { Profile } from "../types/Profile";
import ProjectView from "../components/ProjectView";

export default class MainPage extends React.Component {
    render() {
        return (<div>
            <div className="pb-5">
                <Bootstrap.Row>
                    <Bootstrap.Col>
                        <Section title="專業技能">
                            <ul>
                                <li><span className="h5">程式語言</span></li>
                                <ul>{Profile.profession.languages.map((lang, i) => <li key={i}>{lang}</li>)}</ul>
                                <li><span className="h5">接觸過的 框架</span></li>
                                <ul>
                                    <li><span className="h5">後端</span></li>
                                    <ul>{Profile.profession.frameworks.backend.map((backend, i) =>
                                        <li key={i} className="my-2">
                                            {backend.name}
                                            {backend.tags.map((tag, i) => <span key={i}>&nbsp;<Tag mini>{tag}</Tag></span>)}
                                        </li>
                                    )}</ul>
                                    <li><span className="h5">前端</span></li>
                                    <ul>{Profile.profession.frameworks.frontend.map((frontend, i) =>
                                        <li key={i} className="my-2">
                                            {frontend.name}
                                            {frontend.tags.map((tag, i) => <span key={i}>&nbsp;<Tag mini>{tag}</Tag></span>)}
                                        </li>
                                    )}</ul>
                                </ul>
                                <li><span className="h5">其他</span></li>
                                <ul>{Profile.profession.misc.map((misc, i) => <li key={i}>{misc}</li>)}</ul>
                            </ul>
                        </Section>
                    </Bootstrap.Col>
                    <Bootstrap.Col>
                        <Section title="工作經驗">
                            <ul>
                                {Profile.experiences.map((exp, i) =>
                                    <li key={i}>
                                        <span className="h5">{exp.company} - {exp.position}</span>
                                        &nbsp;
                                        <small style={{ color: "gray" }}>({exp.start} ~ {exp.end})</small>
                                        <ul>
                                            {exp.details.map((content, i) => <li key={i}>{content}</li>)}
                                        </ul>
                                    </li>
                                )}
                            </ul>
                        </Section>
                    </Bootstrap.Col>
                </Bootstrap.Row>
            </div>
            {/* <div className="pb-5">
                <div className="anchor" id="projects-personal"></div>
                <Section title="個人專案">
                    <ProjectView project={Profile.projects.personal}/>
                </Section>
            </div> */}
            <div className="pb-5">
                <div className="anchor" id="projects-school"></div>
                <Section title="學校作品">
                    <ProjectView project={Profile.projects.school}/>
                </Section>
            </div>
        </div>);
    }
}